/* Dynamic typing Text */
.wrapper {
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.wrapper .static-txt {
    /* color: rgb(73, 66, 66); */
    font-size: 25px;
    font-weight: 400;
}

.wrapper .dynamic-txts {
    height: 90px;
    line-height: 90px;
    overflow: hidden;
}

.dynamic-txts li {
    /* color: #1EA896; */
    list-style: none;
    font-size: 45px;
    font-weight: 500;
    position: relative;
    top: 0;
    animation: slide 12s steps(4) infinite;
}

@keyframes slide {
    100% {
        top: -360px;
    }
}

.dynamic-txts li span {
    position: relative;
    margin: 5px 0;
    line-height: 90px;
}

.dynamic-txts li span::after {
    /* border-left: 2px solid #FC6D6D; */
    content: "";
    position: absolute;
    left: 0;
    height: 130%;
    width: 100%;
    background: #EBF2FA;
    animation: typing 3s steps(10) infinite;
}

@keyframes typing {

    40%,
    60% {
        left: calc(100% + 30px);
    }

    100% {
        left: 0;
    }
}


.selectors-item {
    background-color: #f5f9ff;
